  department: string;
  joinDate: string;
  salary: number;
  status: 'active' | 'inactive' | 'on_leave';
  avatar: string;
  contractType: EmploymentType;
  contractStartDate?: string;
  contractEndDate?: string;
  endOfServiceDate?: string;
  shiftId?: string;
  shiftName?: string;
  isDriver?: boolean;
  driverLicenseNumber?: string;
  driverLicenseExpiry?: string;
  certificates?: { 
    id: string;
    name: string; 
    date: string; 
    fileName?: string;
    fileUrl?: string; 
  }[];
  documents?: {
    id: string;
    name: string;
    type: string;
    size: string;
    date: string;
    fileUrl?: string;
  }[];
