
// src/services/api.ts

// Get Base URL from settings or default to localhost
export const getBaseUrl = () => {
  const savedUrl = localStorage.getItem('server_url');
  // Remove trailing slash if exists
  return savedUrl ? savedUrl.replace(/\/$/, '') : 'http://localhost:5000/api';
};

// Helper function for requests
const request = async (endpoint: string, options: RequestInit = {}) => {
  const baseUrl = getBaseUrl();
  try {
    // Increase timeout to 20 seconds for slow biometric devices
    const controller = new AbortController();
    const timeoutId = setTimeout(() => controller.abort(), 20000);
    
    const response = await fetch(`${baseUrl}${endpoint}`, {
      signal: controller.signal,
      headers: {
        'Content-Type': 'application/json',
        ...options.headers,
      },
      ...options,
    });
    
    clearTimeout(timeoutId);

    if (!response.ok) {
      // Try to parse the error message from the server
      const errorData = await response.json().catch(() => ({}));
      const errorMessage = errorData.message || errorData.error || response.statusText;
      throw new Error(errorMessage);
    }

    return await response.json();
  } catch (error: any) {
    // Don't log health check errors to console to avoid noise
    if (endpoint !== '/health') {
        console.warn(`Backend request failed for ${endpoint}:`, error);
    }
    throw error;
  }
};

export const api = {
  // Check if backend is running
  checkHealth: () => request('/health'),

  // Sync Attendance from a specific device for a specific date
  syncAttendance: (deviceIp: string, devicePort: string, deviceType: string, date: string) => 
    request('/attendance/sync', {
      method: 'POST',
      body: JSON.stringify({ ip: deviceIp, port: devicePort, type: deviceType, date: date }),
    }),

  // Get Live GPS Data
  getVehiclesLocation: () => request('/transport/vehicles'),

  // Scan network for devices
  scanForDevices: () => request('/network/scan'),
};
