
import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { MOCK_EMPLOYEES, DEPARTMENTS as DEFAULT_DEPTS, MOCK_SHIFTS } from '../constants';
import { Search, Plus, Filter, MoreVertical, FileText, User, CreditCard, CalendarOff, Contact, X, Printer, QrCode, Trash2, Save, Building2, Clock } from 'lucide-react';
import { Employee, EmploymentType, Shift } from '../types';
import DataControls from '../components/DataControls';

const Employees: React.FC = () => {
  const [employees, setEmployees] = useState<Employee[]>(() => {
    const saved = localStorage.getItem('employees_data');
    if (saved) return JSON.parse(saved);
    const activeDb = localStorage.getItem('active_db_id');
    return (!activeDb || activeDb === 'DB1') ? MOCK_EMPLOYEES : [];
  });

  const [shifts, setShifts] = useState<Shift[]>(() => {
    const saved = localStorage.getItem('shifts_data');
    if (saved) return JSON.parse(saved);
    const activeDb = localStorage.getItem('active_db_id');
    return (!activeDb || activeDb === 'DB1') ? MOCK_SHIFTS : [];
  });

  // Departments State
  const [departments, setDepartments] = useState<string[]>(() => {
      const saved = localStorage.getItem('system_departments');
      return saved ? JSON.parse(saved) : DEFAULT_DEPTS;
  });
  
  // Global Search
  const [searchTerm, setSearchTerm] = useState('');
  
  // Column Filters State
  const [colFilters, setColFilters] = useState({
    code: '',
    name: '',
    department: 'all',
    nationalId: '',
    joinDate: '',
    status: 'all'
  });
  
  // ID Card State
  const [cardEmployee, setCardEmployee] = useState<Employee | null>(null);
  
  // Menu State
  const [openMenuId, setOpenMenuId] = useState<string | null>(null);

  // Add Employee Modal State
  const [isAddModalOpen, setIsAddModalOpen] = useState(false);
  const [newEmployee, setNewEmployee] = useState<Partial<Employee>>({
    name: '',
    nationalId: '',
    jobTitle: '',
    department: '',
    salary: 0,
    contractType: EmploymentType.FULL_TIME,
    status: 'active',
    joinDate: new Date().toISOString().split('T')[0],
    employeeCode: '',
    shiftId: ''
  });

  const navigate = useNavigate();

  // Simulated Admin Status
  const isAdmin = true;

  // Persist changes
  useEffect(() => {
    localStorage.setItem('employees_data', JSON.stringify(employees));
  }, [employees]);

  // Load shifts and departments on mount (in case updated from other tab)
  useEffect(() => {
      const savedShifts = localStorage.getItem('shifts_data');
      if (savedShifts) setShifts(JSON.parse(savedShifts));
      
      const savedDepts = localStorage.getItem('system_departments');
      if (savedDepts) setDepartments(JSON.parse(savedDepts));
  }, []);

  const filteredEmployees = employees.filter(emp => {
    // 1. Global Search
    const matchesGlobal = emp.name.includes(searchTerm) || 
                          emp.nationalId.includes(searchTerm) || 
                          emp.employeeCode.includes(searchTerm);

    // 2. Column Filters
    const matchesCode = emp.employeeCode.toLowerCase().includes(colFilters.code.toLowerCase());
    const matchesName = emp.name.toLowerCase().includes(colFilters.name.toLowerCase());
    const matchesDept = colFilters.department === 'all' || emp.department === colFilters.department;
    const matchesNID = emp.nationalId.includes(colFilters.nationalId);
    const matchesDate = emp.joinDate.includes(colFilters.joinDate);
    const matchesStatus = colFilters.status === 'all' || emp.status === colFilters.status;
    
    return matchesGlobal && matchesCode && matchesName && matchesDept && matchesNID && matchesDate && matchesStatus;
  });

  const handleImport = (data: any[]) => {
    const newEmployees = data.map((d: any, idx) => ({
      ...MOCK_EMPLOYEES[0],
      ...d,
      id: `IMP-${Date.now()}-${idx}`
    }));
    setEmployees(prev => [...prev, ...newEmployees]);
  };

  const handleAddEmployee = (e: React.FormEvent) => {
    e.preventDefault();
    
    // Find Shift Name
    const selectedShift = shifts.find(s => s.id === newEmployee.shiftId);

    const employee: Employee = {
      id: `E${Date.now()}`,
      employeeCode: newEmployee.employeeCode || `EMP-${Math.floor(Math.random() * 10000)}`,
      name: newEmployee.name || '',
      nationalId: newEmployee.nationalId || '',
      jobTitle: newEmployee.jobTitle || '',
      department: newEmployee.department || '',
      joinDate: newEmployee.joinDate || '',
      salary: Number(newEmployee.salary) || 0,
      status: 'active',
      avatar: `https://ui-avatars.com/api/?name=${newEmployee.name}&background=random`,
      contractType: newEmployee.contractType as EmploymentType,
      shiftId: newEmployee.shiftId,
      shiftName: selectedShift ? selectedShift.name : ''
    };

    setEmployees([...employees, employee]);
    setIsAddModalOpen(false);
    setNewEmployee({
      name: '',
      nationalId: '',
      jobTitle: '',
      department: '',
      salary: 0,
      contractType: EmploymentType.FULL_TIME,
      status: 'active',
      joinDate: new Date().toISOString().split('T')[0],
      employeeCode: '',
      shiftId: ''
    });
    alert('   ');
  };

  const handleDeleteEmployee = (id: string) => {
    if (window.confirm('       ')) {
      setEmployees(employees.filter(e => e.id !== id));
    }
    setOpenMenuId(null);
  };

  const handlePrintCard = () => {
    const printContent = document.getElementById('employee-id-card-content');
    if (!printContent) return;

    const printWindow = window.open('', '_blank', 'width=800,height=600');
    if (printWindow) {
      printWindow.document.write(`
        <html dir="rtl">
          <head>
            <title>  - ${cardEmployee?.name}</title>
            <script src="https://cdn.tailwindcss.com"></script>
            <link href="https://fonts.googleapis.com/css2?family=Cairo:wght@400;600;700&display=swap" rel="stylesheet">
            <style>
              body { font-family: 'Cairo', sans-serif; background-color: #fff; display: flex; justify-content: center; align-items: center; min-height: 100vh; -webkit-print-color-adjust: exact; }
              .card-container { display: flex; gap: 20px; flex-direction: column; }
              @media print {
                body { background-color: #fff; margin: 0; padding: 20px; }
                .no-print { display: none; }
              }
            </style>
          </head>
          <body>
            <div class="card-container">
              ${printContent.innerHTML}
            </div>
            <script>
              setTimeout(() => {
                window.print();
                window.close();
              }, 500);
            </script>
          </body>
        </html>
      `);
      printWindow.document.close();
    }
  };

  return (
    <div className="space-y-6" onClick={() => setOpenMenuId(null)}>
      <div className="flex flex-col justify-between gap-4 sm:flex-row sm:items-center">
        <div>
          <h2 className="text-2xl font-bold text-gray-800"> </h2>
          <p className="text-sm text-gray-500">     </p>
        </div>
        <div className="flex gap-3">
          <DataControls 
            data={employees} 
            fileName="employees_data" 
            onImport={handleImport}
            isAdmin={isAdmin}
            headers={[
              { key: 'employeeCode', label: ' ' },
              { key: 'name', label: '' },
              { key: 'nationalId', label: ' ' },
              { key: 'jobTitle', label: '' },
              { key: 'department', label: '' },
              { key: 'contractType', label: ' ' },
              { key: 'salary', label: '' },
              { key: 'joinDate', label: ' ' },
              { key: 'shiftName', label: '' },
              { key: 'endOfServiceDate', label: '  ' }
            ]}
          />
          <button 
            onClick={() => setIsAddModalOpen(true)}
            className="flex items-center gap-2 rounded-lg bg-indigo-600 px-4 py-2 text-white shadow-sm hover:bg-indigo-700 transition"
          >
            <Plus className="h-5 w-5" />
            <span className="hidden sm:inline"> </span>
          </button>
        </div>
      </div>

      <div className="rounded-xl bg-white shadow-sm border border-gray-100">
        <div className="flex flex-col gap-4 border-b border-gray-100 p-4 lg:flex-row lg:items-center justify-between">
          <div className="relative w-full lg:w-96">
            <Search className="absolute right-3 top-1/2 h-5 w-5 -translate-y-1/2 text-gray-400" />
            <input 
              type="text" 
              placeholder=" ..."
              className="w-full rounded-lg border border-gray-200 py-2 pr-10 pl-4 focus:border-indigo-500 focus:outline-none focus:ring-1 focus:ring-indigo-500"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />
          </div>
        </div>

        <div className="overflow-x-auto min-h-[400px]">
          <table className="w-full text-right text-sm">
            <thead className="bg-gray-50 text-gray-500">
              <tr>
                <th className="px-6 py-4 font-medium"> </th>
                <th className="px-6 py-4 font-medium"></th>
                <th className="px-6 py-4 font-medium"> / </th>
                <th className="px-6 py-4 font-medium"> </th>
                <th className="px-6 py-4 font-medium"> </th>
                <th className="px-6 py-4 font-medium"></th>
                <th className="px-6 py-4 font-medium"></th>
                <th className="px-6 py-4 font-medium"></th>
              </tr>
              {/* Filter Row */}
              <tr className="bg-gray-50 border-b border-gray-100">
                <th className="px-6 py-2">
                  <input 
                    type="text" 
                    placeholder=" ..." 
                    className="w-full text-xs p-1.5 border border-gray-300 rounded focus:border-indigo-500 focus:outline-none"
                    value={colFilters.code}
                    onChange={(e) => setColFilters({...colFilters, code: e.target.value})}
                  />
                </th>
                <th className="px-6 py-2">
                  <input 
                    type="text" 
                    placeholder=" ..." 
                    className="w-full text-xs p-1.5 border border-gray-300 rounded focus:border-indigo-500 focus:outline-none"
                    value={colFilters.name}
                    onChange={(e) => setColFilters({...colFilters, name: e.target.value})}
                  />
                </th>
                <th className="px-6 py-2">
                  <select 
                    className="w-full text-xs p-1.5 border border-gray-300 rounded focus:border-indigo-500 focus:outline-none"
                    value={colFilters.department}
                    onChange={(e) => setColFilters({...colFilters, department: e.target.value})}
                  >
                    <option value="all"></option>
                    {departments.map(dept => <option key={dept} value={dept}>{dept}</option>)}
                  </select>
                </th>
                <th className="px-6 py-2">
                  <input 
                    type="text" 
                    placeholder=" ..." 
                    className="w-full text-xs p-1.5 border border-gray-300 rounded focus:border-indigo-500 focus:outline-none"
                    value={colFilters.nationalId}
                    onChange={(e) => setColFilters({...colFilters, nationalId: e.target.value})}
                  />
                </th>
                <th className="px-6 py-2">
                  <input 
                    type="text" 
                    placeholder="YYYY-MM-DD" 
                    className="w-full text-xs p-1.5 border border-gray-300 rounded focus:border-indigo-500 focus:outline-none"
                    value={colFilters.joinDate}
                    onChange={(e) => setColFilters({...colFilters, joinDate: e.target.value})}
                  />
                </th>
                <th className="px-6 py-2"></th>
                <th className="px-6 py-2">
                  <select 
                    className="w-full text-xs p-1.5 border border-gray-300 rounded focus:border-indigo-500 focus:outline-none"
                    value={colFilters.status}
                    onChange={(e) => setColFilters({...colFilters, status: e.target.value})}
                  >
                    <option value="all"></option>
                    <option value="active"></option>
                    <option value="on_leave"></option>
                    <option value="inactive"></option>
                  </select>
                </th>
                <th className="px-6 py-2"></th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-100">
              {filteredEmployees.map((emp) => (
                <tr 
                  key={emp.id} 
                  onClick={() => navigate(`/employees/${emp.id}`)}
                  className="hover:bg-gray-50 transition cursor-pointer"
                >
                  <td className="px-6 py-4">
                    <span className="font-mono font-bold text-gray-600 bg-gray-50 px-2 py-1 rounded">{emp.employeeCode}</span>
                  </td>
                  <td className="px-6 py-4">
                    <div className="flex items-center gap-3">
                      <img src={emp.avatar} alt="" className="h-10 w-10 rounded-full object-cover" />
                      <div>
                        <p className="font-semibold text-gray-900">{emp.name}</p>
                        <p className="text-xs text-gray-500">{emp.id}</p>
                      </div>
                    </div>
                  </td>
                  <td className="px-6 py-4">
                    <p className="font-medium text-gray-900">{emp.jobTitle}</p>
                    <div className="flex flex-col gap-1 mt-1">
                      <p className="text-xs text-gray-500">{emp.department}</p>
                      <span className="inline-flex w-fit items-center rounded bg-gray-100 px-2 py-0.5 text-[10px] text-gray-600">
                        {emp.contractType}
                      </span>
                    </div>
                  </td>
                  <td className="px-6 py-4">
                     <div className="flex items-center gap-2 text-gray-600" title=" ">
                        <CreditCard className="h-4 w-4 text-gray-400" />
                        <span className="font-mono text-sm">{emp.nationalId}</span>
                     </div>
                  </td>
                  <td className="px-6 py-4">
                    <div className="text-gray-600 font-mono text-sm">{emp.joinDate}</div>
                    {emp.endOfServiceDate && (
                       <div className="flex items-center gap-1 text-red-500 text-xs mt-1 bg-red-50 px-1.5 py-0.5 rounded w-fit" title="  ">
                          <CalendarOff className="h-3 w-3" />
                          <span>{emp.endOfServiceDate}</span>
                       </div>
                    )}
                  </td>
                  <td className="px-6 py-4">
                     <span className="text-xs text-indigo-600 bg-indigo-50 px-2 py-1 rounded border border-indigo-100">
                        {emp.shiftName || ' '}
                     </span>
                  </td>
                  <td className="px-6 py-4">
                    <span className={`inline-flex items-center rounded-full px-2.5 py-0.5 text-xs font-medium
                      ${emp.status === 'active' ? 'bg-green-100 text-green-800' : 
                        emp.status === 'on_leave' ? 'bg-yellow-100 text-yellow-800' : 'bg-red-100 text-red-800'}
                    `}>
                      {emp.status === 'active' ? '' : emp.status === 'on_leave' ? '' : ''}
                    </span>
                  </td>
                  <td className="px-6 py-4 relative">
                    <div className="flex items-center gap-2" onClick={(e) => e.stopPropagation()}>
                       <button 
                         onClick={(e) => { e.stopPropagation(); setCardEmployee(emp); }}
                         className="text-gray-400 hover:text-indigo-600 bg-gray-50 p-1.5 rounded hover:bg-indigo-50 transition-colors"
                         title=" "
                       >
                          <Contact className="h-4 w-4" />
                       </button>
                       <button 
                         onClick={() => navigate(`/employees/${emp.id}`)}
                         className="text-gray-400 hover:text-indigo-600 bg-gray-50 p-1.5 rounded hover:bg-indigo-50 transition-colors" 
                         title=" "
                       >
                          <User className="h-4 w-4" />
                       </button>
                       <button 
                          onClick={() => navigate('/contracts')}
                          className="text-gray-400 hover:text-indigo-600 bg-gray-50 p-1.5 rounded hover:bg-indigo-50 transition-colors" 
                          title=""
                       >
                          <FileText className="h-4 w-4" />
                       </button>
                       
                       <div className="relative">
                          <button 
                             onClick={(e) => { e.stopPropagation(); setOpenMenuId(openMenuId === emp.id ? null : emp.id); }}
                             className="text-gray-400 hover:text-indigo-600 bg-gray-50 p-1.5 rounded hover:bg-indigo-50 transition-colors"
                          >
                             <MoreVertical className="h-4 w-4" />
                          </button>
                          {openMenuId === emp.id && (
                             <div className="absolute left-0 top-8 w-40 bg-white rounded-lg shadow-lg border border-gray-100 z-10 py-1 overflow-hidden">
                                <button className="w-full text-right px-4 py-2 text-xs text-gray-700 hover:bg-gray-50"> </button>
                                <button className="w-full text-right px-4 py-2 text-xs text-gray-700 hover:bg-gray-50"> </button>
                                <button 
                                  onClick={(e) => { e.stopPropagation(); handleDeleteEmployee(emp.id); }}
                                  className="w-full text-right px-4 py-2 text-xs text-red-600 hover:bg-red-50 flex items-center gap-2"
                                >
                                  <Trash2 className="h-3 w-3" /> 
                                </button>
                             </div>
                          )}
                       </div>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
          {filteredEmployees.length === 0 && (
            <div className="p-8 text-center text-gray-500">
                 
            </div>
          )}
        </div>
      </div>

      {/* ID Card Modal (Unchanged...) */}
      {cardEmployee && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/60 p-4 backdrop-blur-sm">
          {/* ... (ID Card Content same as before) ... */}
          <div className="w-full max-w-lg bg-white rounded-2xl shadow-2xl animate-in fade-in zoom-in duration-200 overflow-hidden flex flex-col">
            <div className="p-4 border-b border-gray-100 flex justify-between items-center bg-gray-50">
               <h3 className="font-bold text-gray-800 flex items-center gap-2">
                  <Contact className="h-5 w-5 text-indigo-600" />
                    
               </h3>
               <button onClick={() => setCardEmployee(null)} className="text-gray-400 hover:text-gray-600 rounded-full p-1 hover:bg-gray-200 transition">
                  <X className="h-5 w-5" />
               </button>
            </div>
            
            <div className="p-8 bg-gray-100 flex flex-col items-center gap-6 overflow-y-auto max-h-[70vh]">
               {/* Card Preview Container */}
               <div id="employee-id-card-content" className="flex flex-col gap-6">
                  
                  {/* Front Side */}
                  <div className="w-[320px] h-[500px] bg-white rounded-2xl shadow-lg overflow-hidden relative border border-gray-200">
                     {/* Header Pattern */}
                     <div className="h-32 bg-indigo-600 relative overflow-hidden">
                        <div className="absolute top-0 left-0 w-full h-full opacity-20">
                           <svg className="w-full h-full" viewBox="0 0 100 100" preserveAspectRatio="none">
                              <path d="M0 100 C 20 0 50 0 100 100 Z" fill="white" />
                           </svg>
                        </div>
                        <div className="absolute top-4 left-0 w-full text-center">
                           <h2 className="text-white font-bold text-lg tracking-wide">  </h2>
                           <p className="text-indigo-200 text-xs uppercase tracking-widest">Nizam HR System</p>
                        </div>
                     </div>

                     {/* Profile Image */}
                     <div className="absolute top-20 left-1/2 -translate-x-1/2">
                        <div className="w-28 h-28 rounded-full border-4 border-white shadow-md overflow-hidden bg-white">
                           <img src={cardEmployee.avatar} alt="Employee" className="w-full h-full object-cover" />
                        </div>
                     </div>

                     {/* Details */}
                     <div className="mt-20 text-center px-6">
                        <h1 className="text-xl font-bold text-gray-800 mb-1">{cardEmployee.name}</h1>
                        <p className="text-indigo-600 font-medium text-sm mb-4">{cardEmployee.jobTitle}</p>
                        
                        <div className="space-y-3 text-sm text-gray-600 bg-gray-50 p-4 rounded-xl border border-gray-100">
                           <div className="flex justify-between border-b border-gray-200 pb-2">
                              <span>:</span>
                              <span className="font-semibold text-gray-800">{cardEmployee.department}</span>
                           </div>
                           <div className="flex justify-between border-b border-gray-200 pb-2">
                              <span>:</span>
                              <span className="font-mono font-bold text-gray-800">{cardEmployee.employeeCode}</span>
                           </div>
                           <div className="flex justify-between">
                              <span> :</span>
                              <span className="font-mono text-gray-800">{cardEmployee.joinDate}</span>
                           </div>
                        </div>
                     </div>

                     {/* Barcode / Footer */}
                     <div className="absolute bottom-0 w-full p-4 bg-gray-50 border-t border-gray-100 flex flex-col items-center">
                        {/* Simulated Barcode */}
                        <div className="h-8 w-48 flex items-end justify-center gap-[2px] opacity-70">
                           {[...Array(30)].map((_,i) => (
                              <div key={i} className={`bg-black h-full w-[${Math.random() > 0.5 ? '2px' : '4px'}]`}></div>
                           ))}
                        </div>
                        <p className="text-[10px] text-gray-400 mt-1">{cardEmployee.id}</p>
                     </div>
                  </div>

                  {/* Back Side (Optional Visual) */}
                  <div className="w-[320px] h-[500px] bg-white rounded-2xl shadow-lg overflow-hidden relative border border-gray-200 flex flex-col p-6 text-center">
                     <div className="mb-6">
                        <QrCode className="w-24 h-24 text-gray-800 mx-auto mb-2" />
                        <p className="text-xs text-gray-500">Scan for Profile</p>
                     </div>
                     
                     <div className="text-right space-y-4 text-xs text-gray-600 mb-auto">
                        <p className="font-bold text-gray-800 border-b pb-1">:</p>
                        <ul className="list-disc pr-4 space-y-1">
                           <li>       .</li>
                           <li>         .</li>
                           <li>     .</li>
                        </ul>
                        
                        <div className="pt-4 mt-4 border-t">
                           <p className="font-bold text-gray-800 mb-1">  :</p>
                           <p>  : <span className="font-mono" dir="ltr">+20 123 456 7890</span></p>
                        </div>
                     </div>

                     <div className="mt-4 pt-4 border-t border-gray-100">
                        <p className="text-xs text-gray-400">Issued by Nizam HR</p>
                        <p className="text-[10px] text-gray-300 mt-1">www.nizam-hr.com</p>
                     </div>
                  </div>

               </div>
            </div>

            <div className="p-4 border-t border-gray-100 bg-gray-50 flex justify-end gap-3">
               <button 
                  onClick={() => setCardEmployee(null)}
                  className="px-4 py-2 border border-gray-300 bg-white rounded-lg text-gray-700 hover:bg-gray-50 text-sm font-medium"
               >
                  
               </button>
               <button 
                  onClick={handlePrintCard}
                  className="px-4 py-2 bg-indigo-600 text-white rounded-lg hover:bg-indigo-700 text-sm font-medium flex items-center gap-2 shadow-sm"
               >
                  <Printer className="h-4 w-4" />
                   
               </button>
            </div>
          </div>
        </div>
      )}

      {/* Add Employee Modal */}
      {isAddModalOpen && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 p-4 backdrop-blur-sm">
           <div className="w-full max-w-3xl bg-white rounded-2xl shadow-2xl animate-in fade-in zoom-in duration-200 flex flex-col max-h-[90vh]">
              <div className="flex items-center justify-between border-b border-gray-100 p-6 bg-gray-50 rounded-t-2xl">
                 <h3 className="font-bold text-gray-800 flex items-center gap-2">
                    <Plus className="h-5 w-5 text-indigo-600" />
                      
                 </h3>
                 <button onClick={() => setIsAddModalOpen(false)} className="rounded-full p-2 text-gray-400 hover:bg-gray-200 hover:text-gray-700 transition">
                    <X className="h-6 w-6" />
                 </button>
              </div>
              
              <div className="p-6 overflow-y-auto flex-1">
                 <form onSubmit={handleAddEmployee} className="grid grid-cols-1 md:grid-cols-2 gap-5">
                    <div className="md:col-span-2">
                       <label className="block text-sm font-medium text-gray-700 mb-1"> </label>
                       <input 
                          type="text" required 
                          className="w-full p-2 rounded-lg border border-gray-300"
                          value={newEmployee.name}
                          onChange={(e) => setNewEmployee({...newEmployee, name: e.target.value})}
                       />
                    </div>
                    <div>
                       <label className="block text-sm font-medium text-gray-700 mb-1"> </label>
                       <input 
                          type="text" required 
                          className="w-full p-2 rounded-lg border border-gray-300"
                          value={newEmployee.jobTitle}
                          onChange={(e) => setNewEmployee({...newEmployee, jobTitle: e.target.value})}
                       />
                    </div>
                    <div>
                       <label className="block text-sm font-medium text-gray-700 mb-1"></label>
                       <select 
                          required 
                          className="w-full p-2 rounded-lg border border-gray-300"
                          value={newEmployee.department}
                          onChange={(e) => setNewEmployee({...newEmployee, department: e.target.value})}
                       >
                          <option value=""> </option>
                          {departments.map(dept => (
                             <option key={dept} value={dept}>{dept}</option>
                          ))}
                       </select>
                    </div>
                    <div>
                       <label className="block text-sm font-medium text-gray-700 mb-1"> </label>
                       <input 
                          type="number" required 
                          className="w-full p-2 rounded-lg border border-gray-300"
                          value={newEmployee.salary}
                          onChange={(e) => setNewEmployee({...newEmployee, salary: Number(e.target.value)})}
                       />
                    </div>
                    <div>
                       <label className="block text-sm font-medium text-gray-700 mb-1"> </label>
                       <select 
                          className="w-full p-2 rounded-lg border border-gray-300"
                          value={newEmployee.contractType}
                          onChange={(e) => setNewEmployee({...newEmployee, contractType: e.target.value as EmploymentType})}
                       >
                          {(Object.values(EmploymentType) as string[]).map(type => (
                             <option key={type} value={type}>{type}</option>
                          ))}
                       </select>
                    </div>
                    <div>
                       <label className="block text-sm font-medium text-gray-700 mb-1"></label>
                       <select 
                          className="w-full p-2 rounded-lg border border-gray-300"
                          value={newEmployee.shiftId}
                          onChange={(e) => setNewEmployee({...newEmployee, shiftId: e.target.value})}
                       >
                          <option value="">  ()</option>
                          {shifts.map(shift => (
                             <option key={shift.id} value={shift.id}>{shift.name} ({shift.startTime} - {shift.endTime})</option>
                          ))}
                       </select>
                    </div>
                    <div>
                       <label className="block text-sm font-medium text-gray-700 mb-1"> </label>
                       <input 
                          type="text" required 
                          className="w-full p-2 rounded-lg border border-gray-300 font-mono"
                          value={newEmployee.employeeCode}
                          onChange={(e) => setNewEmployee({...newEmployee, employeeCode: e.target.value})}
                          placeholder="EMP-XXXX"
                       />
                    </div>
                    <div>
                       <label className="block text-sm font-medium text-gray-700 mb-1"> </label>
                       <input 
                          type="text" required 
                          className="w-full p-2 rounded-lg border border-gray-300 font-mono"
                          value={newEmployee.nationalId}
                          onChange={(e) => setNewEmployee({...newEmployee, nationalId: e.target.value})}
                       />
                    </div>
                    <div>
                       <label className="block text-sm font-medium text-gray-700 mb-1"> </label>
                       <input 
                          type="date" required 
                          className="w-full p-2 rounded-lg border border-gray-300"
                          value={newEmployee.joinDate}
                          onChange={(e) => setNewEmployee({...newEmployee, joinDate: e.target.value})}
                       />
                    </div>
                 </form>
              </div>

              <div className="p-4 bg-gray-50 border-t border-gray-100 flex justify-end gap-3 rounded-b-2xl">
                 <button 
                    onClick={() => setIsAddModalOpen(false)}
                    className="px-4 py-2 border border-gray-300 bg-white rounded-lg text-gray-700 hover:bg-gray-100 text-sm font-medium"
                 >
                    
                 </button>
                 <button 
                    onClick={handleAddEmployee}
                    className="px-4 py-2 bg-indigo-600 text-white rounded-lg hover:bg-indigo-700 text-sm font-medium flex items-center gap-2 shadow-sm"
                 >
                    <Save className="h-4 w-4" />
                     
                 </button>
              </div>
           </div>
        </div>
      )}
    </div>
  );
};

export default Employees;
