import React, { useState } from 'react';
import { 
  FileBarChart, Users, Clock, DollarSign, PieChart, Briefcase, 
  Printer, X, Wallet, Filter, Calendar, FileText, ArrowLeft, Download
} from 'lucide-react';
import { 
  MOCK_ATTENDANCE, 
  MOCK_PAYROLL, 
  MOCK_EMPLOYEES, 
  MOCK_CANDIDATES, 
  MOCK_LEAVE_BALANCES,
  MOCK_LOANS
} from '../constants';
import DataControls from '../components/DataControls';

interface ReportType {
  id: number;
  title: string;
  desc: string;
  icon: any;
  color: string;
  bg: string;
  dataKey: string;
}

const Reports: React.FC = () => {
  const [selectedReport, setSelectedReport] = useState<ReportType | null>(null);
  
  // Helper to get employee code
  const getEmpCode = (name: string) => MOCK_EMPLOYEES.find(e => e.name === name)?.employeeCode || '-';

  const reports: ReportType[] = [
    { id: 1, title: '  ', desc: '    ', icon: Clock, color: 'text-indigo-600', bg: 'bg-indigo-50', dataKey: 'attendance' },
    { id: 2, title: '  ', desc: '   ', icon: DollarSign, color: 'text-green-600', bg: 'bg-green-50', dataKey: 'payroll' },
    { id: 3, title: '  ', desc: '     ', icon: Users, color: 'text-blue-600', bg: 'bg-blue-50', dataKey: 'employees' },
    { id: 4, title: ' ', desc: '   ', icon: Briefcase, color: 'text-purple-600', bg: 'bg-purple-50', dataKey: 'recruitment' },
    { id: 5, title: ' ', desc: '    ', icon: PieChart, color: 'text-orange-600', bg: 'bg-orange-50', dataKey: 'leaves' },
    { id: 6, title: '  ', desc: '      ', icon: Wallet, color: 'text-red-600', bg: 'bg-red-50', dataKey: 'loans' },
  ];

  const handlePrint = () => {
    window.print();
  };

  const renderReportContent = () => {
    if (!selectedReport) return null;

    switch (selectedReport.dataKey) {
      case 'attendance':
        const enrichedAttendance = MOCK_ATTENDANCE.map(r => ({...r, employeeCode: getEmpCode(r.employeeName)}));
        return (
          <div className="space-y-4">
             <div className="mb-4 flex justify-end no-print">
                <DataControls data={enrichedAttendance} fileName="attendance_report" isAdmin={true} />
             </div>
             <table className="w-full text-right text-sm border border-gray-200">
              <thead className="bg-gray-50 text-gray-700 font-bold">
                <tr>
                  <th className="p-3 border border-gray-200 w-20"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                </tr>
              </thead>
              <tbody>
                {enrichedAttendance.map((row, idx) => (
                  <tr key={idx} className="border-b border-gray-100">
                    <td className="p-3 border-l border-gray-100 font-mono text-gray-500">{row.employeeCode}</td>
                    <td className="p-3 border-l border-gray-100">{row.employeeName}</td>
                    <td className="p-3 border-l border-gray-100">{row.date}</td>
                    <td className="p-3 border-l border-gray-100">{row.checkIn}</td>
                    <td className="p-3 border-l border-gray-100">{row.checkOut}</td>
                    <td className="p-3 border-l border-gray-100">{row.status}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        );
      case 'payroll':
        const filteredPayroll = MOCK_PAYROLL.map(p => ({...p, employeeCode: getEmpCode(p.employeeName)}));
        return (
          <div className="space-y-4">
             <div className="mb-4 flex justify-end no-print">
                   <DataControls 
                      data={filteredPayroll} 
                      fileName="payroll_report" 
                      isAdmin={true}
                   />
             </div>
             <table className="w-full text-right text-sm border border-gray-200">
              <thead className="bg-gray-50 text-gray-700 font-bold">
                <tr>
                  <th className="p-3 border border-gray-200 w-20"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                </tr>
              </thead>
              <tbody>
                {filteredPayroll.map((row, idx) => (
                    <tr key={idx} className="border-b border-gray-100">
                      <td className="p-3 border-l border-gray-100 font-mono text-gray-500">{row.employeeCode}</td>
                      <td className="p-3 border-l border-gray-100">{row.employeeName}</td>
                      <td className="p-3 border-l border-gray-100">{row.basicSalary.toLocaleString()}</td>
                      <td className="p-3 border-l border-gray-100">{row.allowances.toLocaleString()}</td>
                      <td className="p-3 border-l border-gray-100 text-red-600">{row.deductions.toLocaleString()}</td>
                      <td className="p-3 border-l border-gray-100 font-bold">{row.netSalary.toLocaleString()}</td>
                    </tr>
                ))}
              </tbody>
            </table>
          </div>
        );
      case 'employees':
        return (
          <div className="space-y-4">
             <div className="mb-4 flex justify-end no-print">
                <DataControls data={MOCK_EMPLOYEES} fileName="employees_report" isAdmin={true} />
             </div>
             <table className="w-full text-right text-sm border border-gray-200">
              <thead className="bg-gray-50 text-gray-700 font-bold">
                <tr>
                  <th className="p-3 border border-gray-200 w-20"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"> </th>
                  <th className="p-3 border border-gray-200"></th>
                </tr>
              </thead>
              <tbody>
                {MOCK_EMPLOYEES.map((row, idx) => (
                  <tr key={idx} className="border-b border-gray-100">
                    <td className="p-3 border-l border-gray-100 font-mono text-gray-500">{row.employeeCode}</td>
                    <td className="p-3 border-l border-gray-100">{row.name}</td>
                    <td className="p-3 border-l border-gray-100">{row.jobTitle}</td>
                    <td className="p-3 border-l border-gray-100">{row.department}</td>
                    <td className="p-3 border-l border-gray-100">{row.joinDate}</td>
                    <td className="p-3 border-l border-gray-100">{row.salary}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        );
      case 'recruitment':
        return (
          <div className="space-y-4">
             <div className="mb-4 flex justify-end no-print">
                <DataControls data={MOCK_CANDIDATES} fileName="recruitment_report" isAdmin={true} />
             </div>
             <table className="w-full text-right text-sm border border-gray-200">
              <thead className="bg-gray-50 text-gray-700 font-bold">
                <tr>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                </tr>
              </thead>
              <tbody>
                {MOCK_CANDIDATES.map((row, idx) => (
                  <tr key={idx} className="border-b border-gray-100">
                    <td className="p-3 border-l border-gray-100">{row.name}</td>
                    <td className="p-3 border-l border-gray-100">{row.position}</td>
                    <td className="p-3 border-l border-gray-100">{row.experience}</td>
                    <td className="p-3 border-l border-gray-100">{row.status}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        );
      case 'leaves':
        const enrichedLeaves = MOCK_LEAVE_BALANCES.map(b => ({...b, employeeCode: getEmpCode(b.employeeName)}));
        return (
          <div className="space-y-4">
             <div className="mb-4 flex justify-end no-print">
                <DataControls data={enrichedLeaves} fileName="leaves_balance_report" isAdmin={true} />
             </div>
             <table className="w-full text-right text-sm border border-gray-200">
              <thead className="bg-gray-50 text-gray-700 font-bold">
                <tr>
                  <th className="p-3 border border-gray-200 w-20"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"> </th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"> </th>
                  <th className="p-3 border border-gray-200"></th>
                </tr>
              </thead>
              <tbody>
                {enrichedLeaves.map((row, idx) => (
                  <tr key={idx} className="border-b border-gray-100">
                    <td className="p-3 border-l border-gray-100 font-mono text-gray-500">{row.employeeCode}</td>
                    <td className="p-3 border-l border-gray-100">{row.employeeName}</td>
                    <td className="p-3 border-l border-gray-100">{row.annualTotal}</td>
                    <td className="p-3 border-l border-gray-100">{row.annualUsed}</td>
                    <td className="p-3 border-l border-gray-100">{row.sickTotal}</td>
                    <td className="p-3 border-l border-gray-100">{row.sickUsed}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        );
      case 'loans':
        const filteredLoans = MOCK_LOANS.filter(loan => loan.type === '').map(l => ({...l, employeeCode: getEmpCode(l.employeeName)}));
        return (
          <div className="space-y-4">
             <div className="mb-4 flex justify-end no-print">
                   <DataControls data={filteredLoans} fileName="active_loans_report" isAdmin={true} />
             </div>
             <table className="w-full text-right text-sm border border-gray-200">
              <thead className="bg-gray-50 text-gray-700 font-bold">
                <tr>
                  <th className="p-3 border border-gray-200 w-20"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"> </th>
                  <th className="p-3 border border-gray-200"> </th>
                  <th className="p-3 border border-gray-200"> </th>
                  <th className="p-3 border border-gray-200"></th>
                </tr>
              </thead>
              <tbody>
                {filteredLoans.map((row, idx) => (
                    <tr key={idx} className="border-b border-gray-100">
                      <td className="p-3 border-l border-gray-100 font-mono text-gray-500">{row.employeeCode}</td>
                      <td className="p-3 border-l border-gray-100">{row.employeeName}</td>
                      <td className="p-3 border-l border-gray-100 font-mono">{row.date}</td>
                      <td className="p-3 border-l border-gray-100 font-bold">{row.amount.toLocaleString()} .</td>
                      <td className="p-3 border-l border-gray-100 text-red-600 font-medium">{row.remainingAmount.toLocaleString()} .</td>
                      <td className="p-3 border-l border-gray-100">
                         <span className={`px-2 py-1 rounded text-xs font-bold ${row.status === 'active' ? 'bg-green-100 text-green-700' : 'bg-gray-100 text-gray-600'}`}>
                            {row.status === 'active' ? '' : ''}
                         </span>
                      </td>
                    </tr>
                ))}
              </tbody>
            </table>
          </div>
        );
      default:
        return <p className="text-center py-10 text-gray-500">    </p>;
    }
  };

  return (
    <div className="space-y-6 animate-in fade-in duration-500">
      <div className="bg-white p-6 rounded-xl shadow-sm border border-gray-100 no-print">
        <h2 className="text-2xl font-bold text-gray-800"> </h2>
        <p className="text-sm text-gray-500 mt-1">      </p>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 no-print">
        {reports.map((report) => (
          <div 
            key={report.id} 
            onClick={() => setSelectedReport(report)}
            className="group relative bg-white p-6 rounded-2xl border border-gray-100 shadow-sm hover:shadow-xl hover:-translate-y-1 transition-all duration-300 cursor-pointer"
          >
            <div className="flex justify-between items-start mb-4 relative z-10">
               <div className={`w-14 h-14 ${report.bg} rounded-2xl flex items-center justify-center shadow-sm`}>
                 <report.icon className={`h-7 w-7 ${report.color}`} />
               </div>
            </div>
            <h3 className="text-xl font-bold text-gray-800 mb-2">{report.title}</h3>
            <p className="text-sm text-gray-500 mb-4">{report.desc}</p>
            <div className="flex items-center justify-between pt-4 border-t border-gray-50">
               <span className="text-xs font-bold text-gray-400 bg-gray-50 px-2 py-1 rounded">PDF, Excel</span>
               <div className="flex items-center gap-2 text-indigo-600 text-sm font-bold opacity-80 group-hover:opacity-100">
                  <span></span>
                  <Printer className="h-4 w-4" />
               </div>
            </div>
          </div>
        ))}
      </div>

      {/* Report Preview Modal */}
      {selectedReport && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 p-4 backdrop-blur-sm modal-overlay-print">
          <div className="bg-white w-full max-w-4xl h-[90vh] rounded-xl shadow-2xl flex flex-col overflow-hidden modal-content-print">
            <div className="p-4 border-b border-gray-200 flex justify-between items-center bg-gray-50 no-print">
              <div className="flex items-center gap-3">
                 <div className={`p-2 rounded-lg ${selectedReport.bg}`}><selectedReport.icon className={`h-5 w-5 ${selectedReport.color}`} /></div>
                 <h3 className="text-lg font-bold text-gray-800">{selectedReport.title}</h3>
              </div>
              <div className="flex items-center gap-2">
                 <button onClick={handlePrint} className="flex items-center gap-2 bg-indigo-600 text-white px-4 py-2 rounded-lg hover:bg-indigo-700 text-sm font-medium"><Printer className="h-4 w-4" /></button>
                 <button onClick={() => setSelectedReport(null)} className="p-2 text-gray-400 hover:text-gray-600 hover:bg-gray-100 rounded-full"><X className="h-5 w-5" /></button>
              </div>
            </div>
            <div className="p-8 overflow-y-auto flex-1 bg-white print:p-0">
               <div className="hidden print-only mb-8 text-center border-b border-gray-200 pb-4">
                  <h1 className="text-2xl font-bold text-gray-900 mb-1">{selectedReport.title}</h1>
                  <p className="text-sm text-gray-500"> : {new Date().toLocaleDateString('ar-EG')}</p>
               </div>
               {renderReportContent()}
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Reports;