import React, { useRef } from 'react';
import { Download } from 'lucide-react';
import html2canvas from 'html2canvas';

const OrgChart: React.FC = () => {
  const chartRef = useRef<HTMLDivElement>(null);

  const handleExportImage = async () => {
    if (chartRef.current) {
      try {
        const canvas = await html2canvas(chartRef.current);
        const dataUrl = canvas.toDataURL('image/png');
        const link = document.createElement('a');
        link.href = dataUrl;
        link.download = 'AMA_HR_OrgChart.png';
        link.click();
      } catch (error) {
        console.error("Export failed", error);
        window.alert("  ");
      }
    }
  };

  const structure = {
    name: ' ',
    role: 'CEO',
    image: 'https://picsum.photos/id/1005/100/100',
    children: [
      {
        name: '  ',
        role: 'HR Manager',
        image: 'https://picsum.photos/id/1011/100/100',
        children: [
           { name: ' ', role: 'Recruiter', image: 'https://picsum.photos/id/1027/100/100' },
           { name: ' ', role: 'Payroll', image: 'https://picsum.photos/id/1025/100/100' }
        ]
      },
      {
        name: '  ',
        role: 'Transport Manager',
        image: 'https://picsum.photos/id/1060/100/100',
        children: [
           { name: ' ', role: 'Supervisor', image: 'https://picsum.photos/id/1012/100/100' },
           { name: ' ', role: 'Maintenance', image: 'https://picsum.photos/id/1014/100/100' }
        ]
      }
    ]
  };

  const renderNode = (node: any) => (
    <div className="flex flex-col items-center mx-4">
      <div className="relative flex flex-col items-center bg-white p-4 rounded-xl border border-gray-200 shadow-sm hover:shadow-md hover:border-indigo-300 transition-all w-48 z-10 cursor-pointer group mb-4">
        <img 
           src={node.image} 
           alt={node.name} 
           className="w-16 h-16 rounded-full object-cover border-4 border-indigo-50 mb-3 group-hover:border-indigo-100 transition-colors shadow-sm"
        />
        <h4 className="font-bold text-gray-800 text-sm text-center">{node.name}</h4>
        <p className="text-xs text-indigo-600 font-medium bg-indigo-50 px-2 py-0.5 rounded mt-1">{node.role}</p>
      </div>
      
      {node.children && node.children.length > 0 && (
        <div className="flex flex-col items-center w-full">
          {/* Vertical Line Down */}
          <div className="h-6 w-px bg-gray-300 -mt-4"></div>
          
          <div className="flex justify-center relative w-full pt-4">
             {/* Horizontal Connector */}
             <div className="absolute top-0 left-0 right-0 h-px bg-gray-300 mx-auto" style={{ width: node.children.length > 1 ? 'calc(100% - 12rem)' : '0px' }}></div>
             
             {/* Vertical Connectors for Children */}
             {node.children.length > 1 && node.children.map((_: any, i: number) => (
                 <div key={i} className="absolute top-0 h-4 w-px bg-gray-300" 
                      style={{ 
                        left: `calc(${100 / node.children.length * i + 100 / node.children.length / 2}%)`
                      }}>
                 </div>
             ))}

             <div className="flex w-full justify-center gap-8">
                 {node.children.map((child: any, index: number) => (
                   <div key={index} className="flex flex-col items-center">
                      <div className="h-4 w-px bg-gray-300 -mt-4 mb-0"></div> 
                      {renderNode(child)}
                   </div>
                 ))}
             </div>
          </div>
        </div>
      )}
    </div>
  );

  return (
    <div className="space-y-6 animate-in fade-in duration-500">
      <div className="bg-white p-6 rounded-xl shadow-sm border border-gray-100 flex justify-between items-center no-print">
        <div>
           <h2 className="text-2xl font-bold text-gray-800"> </h2>
           <p className="text-sm text-gray-500 mt-1">    (AMA HR)</p>
        </div>
        <button onClick={handleExportImage} className="bg-indigo-600 text-white px-4 py-2 rounded-lg hover:bg-indigo-700 text-sm flex items-center gap-2 shadow-md transition-transform active:scale-95">
           <Download className="h-4 w-4" />
           <span> </span>
        </button>
      </div>

      <div className="bg-slate-50/50 border border-gray-200 rounded-xl p-8 overflow-x-auto min-h-[600px] flex justify-center items-start" ref={chartRef}>
         <div className="min-w-fit pt-8 transform scale-90 origin-top">
            {renderNode(structure)}
         </div>
      </div>
    </div>
  );
};

export default OrgChart;